            <div className="content">L’utente  potrà,  in qualunque  momento,  esercitare i  diritti  di cui  agli  articoli da  15  a 22  del  Regolamento nonchè conoscere l’elenco completo dei Responsabili  facendone richiesta  contattando  il Titolare  nei  modi qui  sopra specificati.L’utente, inoltre, qualora vi abbia consentito, potrà opporsi al trattamento per le finalità di cui alla lettera b) (attività di marketing) effettuato mediante l’indirizzo di posta elettronica dell’utente stesso, cliccando su un apposito “<i>link</i>” presente in ogni messaggio di posta elettronica (<i>link</i> di "disiscrizione").Annotazione: "<i>link</i>" è il termine tecnico con il quale si definiscono i collegamenti iper-testuali presenti in un sito internet o in altri tipi di comunicazione elettronica. Normalmente tali collegamenti sono evidenziati da una sottolineatura o dal cambiamento in colore o in dimensioni del testo qualora l'utente vi sovrapponga il puntatore del mouse (normalmente una freccetta che muta in manina alla sovrapposizione su un "<i>link</i>")Modalità di modifica del “profilo” dell’utenteL’utente potrà, in qualunque momento modificare  i dati  forniti  o cancellare  il  proprio profilo,  se  presente, servendosi dell'apposita funzionalità "Modifica dati" presente sul sito, oppure inviando un messaggio di posta elettronica all'indirizzo del Titolare al trattamento dei dati sopra specificato.</div>
            
